<template>
	<section class="wrap">
		<section class="wrap_in" :class="{ unscrollable: isUnscRollable }">
			<!-- 头部标题 -->
			<com-header titles="个人中心">
				<template  slot="style-def"></template>
			</com-header>
			<!-- 头部标题 end -->
			
			<!-- {{ $store.state.memberInfo }} -->
			<!-- 个人信息 -->
			<div class="my_info">
				<div class="m_img">
					<router-link to="myinfo/info">
						<img v-lazy="personalInfo.picURL"  alt="">
					</router-link>
				</div>
				<div class="m_info">
					<h3><router-link to="myinfo/info">{{ personalInfo.memberName }}</router-link></h3>
					<p>{{ personalInfo.vipGradeName?personalInfo.vipGradeName:'普通会员' }}</p>
				</div>
			</div>
			<!-- 个人信息 end -->
			<!-- 订单操作 -->
			<div class="order_fun">
				<div class="item" @click="jumpOrder(2)">
					<div>
						<mt-badge size="small" type="error">{{pendingPaymentNum?pendingPaymentNum:'0'}}</mt-badge>
					</div>
					<p>待付款</p>
				</div>
				<div class="line"></div>
				<div class="item wait_send" @click="jumpOrder(3)">
					<div></div>
					<p>待发货</p>
				</div>
				<div class="line"></div>
				<div class="item wait_take" @click="jumpOrder(4)">
					<div></div>
					<p>待收货</p>
				</div>
				<div class="line"></div>
				<div class="item finished" @click="jumpOrder(5)">
					<div></div>
					<p>已完成</p>
				</div>
			</div>
			<!-- 订单操作 end -->
			<div class="lists all_order">
				<router-link to="/order" ><i></i>全部订单</router-link>	
			</div>
			<div class="lists collect">
				<router-link to="myinfo/collect" ><i></i>我的收藏</router-link>
			</div>
			<div class="lists register">
				<router-link to="myinfo/register" ><i></i>需求登记</router-link>
			</div>
			<div class="lists cfj">
				<router-link to="prescript" ><i></i>处方笺</router-link>
			</div>
			<div class="lists address">
				<router-link to="myinfo/address" ><i></i>收货地址</router-link>
			</div>
			<div class="lists consult ">
				<a href="http://vipwebchat.tq.cn/pageinfo.jsp?version=vip&admiuin=9815275&ltype=1&iscallback=0&page_templete_id=122543&is_message_sms=0&is_send_mail=0&action=acd&acd=1&type_code=123" ><i></i>在线咨询</a>
			</div>
			<div class="lists server">
				<router-link to="myinfo/sale_server" ><i></i>售后服务</router-link>
			</div>
			<div class="lists setup">
				<router-link to="myinfo/setting" ><i></i>设置</router-link>
			</div>

			<!-- 底部 -->
			<com-foot></com-foot>
			<!-- 底部 end -->
		</section>

		<!-- 子页面 -->
	    <transition name="fade">
	    	<router-view 
	    		v-on:changeClass="changeClassOne"
	    		v-on:updateInfo="updateInfoChild"></router-view>
	    </transition>
	    <!-- 子页面 end -->
	</section>
</template>
<script>
import { Header, Button, Badge, Toast } from 'mint-ui';
import comHeaderTitle from '@/components/Common/ComHeaderTitle';
import comFoot from '@/components/Common/ComFoot';

export default {
	name: 'myinfo',
	data() {
		return {
	      isUnscRollable: false, //是否页面不可滚动
	      backParentPage: false, //是否回到父页面
	      pendingPaymentNum: '',//待付款数量
	      personalInfo: this.$store.state.memberInfo, //个人信息
		}
	},
	props: [
		// 'pclass',
	],
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-badge':  Badge,
		comFoot, // 公共底部
		comHeaderTitle, // 公共头部标题
	},
	created : function(){
		const _self = this;

		//获取会员信息
		this.$options.methods.getMemberInfo( this );

		//获取待付款订单
		this.$ajax({
			url: _self.webRoot + '/order/getOrderByMemberIdAndStatus',
			data: JSON.stringify({
				memberId: _self.$store.state.memberId,
				status: [ 1, 9 ],
			}),
			headers: {'Content-Type' : 'application/json;charset=utf-8'},
			transformRequest: [function (data) {
					return data
			}]
		})
		.then(function(res){
			// console.log(res)
			if( res.data.success ){
				_self.pendingPaymentNum = res.data.data.length;
			}else{
				console.log( res.data.msg );
			}
		})
		.catch(function(err){
			console.log(err)
		});
	},
	mounted : function() {
		const _self = this;
		this.$emit('changeClass',false);
	},
	updated (){
	    this.$nextTick(function () {
	      // if( !this.backParentPage ){
	      //   this.isUnscRollable = true;
	      // }
	      
	      //更新个人信息
	      this.personalInfo = this.$store.state.memberInfo; //个人信息
	    }) 
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
  	methods: {
  		//跳转订单
  		jumpOrder ( id ){
  			this.$router.push({ path: '/order', query: { pid: id} });
  		},
	    //上传头像后更新图片
	    updateInfoChild ( data ){
	    	//更新个人信息
	    	this.personalInfo = data;
	    },

  		//公共函数        
  		//会员查询 
        getMemberInfo ( _self ){
          _self.$ajax({
            url: _self.webRoot + '/vipSearchByID',
            data: {
              id: _self.$store.state.memberId,
            }
          })
          .then(function(res){
            // console.log('更新个人信息：', res)
            if( !res.data ){
              console.log('获取个人信息失败')
            }
            // 更新个人信息
            _self.$store.commit('add', {
              addMemberInfo: res.data
            });
          })
          .catch(function(err){
            console.log(err)
          });
        },
  		//显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
  	}
}
</script>

<style scoped>
	@import './Css/Myinfo.css';
</style>


